#barometre th,
#barometre td {
    padding: 3px 5px;
}

#barometre table tr th:nth-child(4) {
    font-size: 14px;
    letter-spacing: 1px;
}
#barometre table tr td:nth-child(1) {
    text-align: center;
    padding: 0 3px;
}
#barometre table tr td:nth-child(2) {
    background-color: var(--gray);
    text-align: right;
}

#barometre table tr td:nth-child(3) {
    padding-right: 20px;
}
#barometre table tr td:nth-child(2n+4) {
    width: 24px;
    padding: 0 0 3px;
    text-align: center;
}
#barometre table tr td:nth-child(2n+5) {
    width: 45px;
}

#barometre table tr td:nth-child(12) {
    padding: 0 5px 0 8px;
    background-color: var(--theme);
    text-align: right;
}

@media (max-width: 500px) {
    #barometre th,
    #barometre td {
        padding: 1px 3px;
    }

    #barometre table tr td:nth-child(2) {
        width: auto;
    }

    #barometre table tr td:nth-child(2n+4) {
        width: 15px;
        padding: 0 0 3px;
        text-align: center;
    }

    #barometre table tr td:nth-child(2n+5) {
        width: 35px;
    }

    #barometre table tr th:nth-child(4) {
        font-size: 10px;
    }

    #barometre table tr th:nth-child(5),
    #barometre table tr td:nth-child(12) {
        display: none;
    }

    #barometre .new::after {
        top: 0 !important;
        left: 15% !important;
    }
}

#barometre h2 {
    margin-bottom: 20px !important;
}

#barometre th {
    color: white;
}

#barometre table tr th:nth-child(4) {
    font-weight: lighter;
    font-family: 'Arial Narrow';
}
#barometre table tr th:nth-child(5) {
    font-weight: lighter;
    white-space: nowrap;
}

#barometre .same {
    background-color: var(--gray);
}
#barometre .same::after {
    vertical-align: bottom;
    content: '=';
}
#barometre .up {
    background-color: var(--green);
}
#barometre .up::after {
    vertical-align: bottom;
    content: '▲';
}
#barometre .down {
    background-color: var(--orange);
}
#barometre .down::after {
    vertical-align: bottom;
    content: '▼';
}

#barometre .new {
    position: relative;
}
#barometre .new::after {
    vertical-align: bottom;
    content: '✦';
    font-size: 1.2em;
    color: var(--theme);
    position: absolute;
    top: 5%;
    left: 17%;
}
#barometre .new.windowsOS::after {
    top: -11%;
    left: 19%;
}

#barometre .carregat {
    background-color: var(--cc) !important;
    color: #000000 !important;
}
